import http from '../axios';

const endpoint = '/minion-devices';

export const create = minion => {
  const body = {
    global_uuid: minion.global_uuid,
  };
  return http.post(endpoint, body);
};

export const bulkCreate = minios => {
  return Promise.all(minios.map(minion => create(minion)));
};

export const fetch = () => {
  return http.get(endpoint);
};

export const discoverDevices = () => {
  return http.get(`${endpoint}/discover`);
};

export const remove = minion => {
  const globalUUID = minion.global_uuid
  return http.delete(`${endpoint}/${globalUUID}`);
};

export const bulkRemove = minios => {
  return Promise.all(minios.map(minion => remove(minion)));
};
