<template>
  <div>
    <page-title title="Master Slave Mapping"></page-title>
    <v-tabs v-model="tab" dark>
      <v-tab :key="0">Discover Devices </v-tab>
      <v-tab :key="1">Slave Devices </v-tab>
    </v-tabs>
    <v-tabs-items v-model="tab">
      <v-tab-item :key="0">
        <discover-devices
          :items="discoveredDevices"
          :create-minions="createMinions"
          :is-fetching="isFetchingDevices"
        ></discover-devices>
      </v-tab-item>
      <v-tab-item :key="1">
        <minion-devices
          :items="minionDevices"
          :removeMinions="removeMinions"
          :is-fetching="isFetchingMinionDevices"
        ></minion-devices>
      </v-tab-item>
    </v-tabs-items>
  </div>
</template>
<script>
import PageTitle from '../Common/PageTitle';
import MinionDevices from './MinionDevices';
import DiscoverDevices from './DiscoverDevices';
import * as minionDeviceService from '../../services/minionDevicesService';

export default {
  name: 'master-minion-page',
  components: {
    PageTitle,
    DiscoverDevices,
    MinionDevices,
  },
  watch: {
    '$route.query.tab': function (val) {
      this.tab = this.tabs.indexOf(val);
    },
    'tab': function (val, oldVal) {
      const { tab } = this.$route.query;
      if (tab !== this.tabs[val]) {
        const tabKey = this.tabs[val];
        this.$router.push({ path: 'master-minions', query: { tab: tabKey } });
      }
    },
  },
  data() {
    return {
      tab: 0,
      tabs: ['discover-devices', 'slave-devices'],
      minionDevices: [],
      devicesMap: {},
      minionDevicesMap: {},
      discoveredDevices: [],
      isFetchingMinionDevices: false,
      isFetchingDevices: false,
    };
  },
  mounted() {
    const { tab: tabKey } = this.$route.query;
    this.tab = this.tabs.indexOf(tabKey);
  },
  created() {
    this.syncDevices();
  },
  methods: {
    formatAddress(item) {
      let address = '';
      if (item.site_address) {
        address += item.site_address;
      }

      if (item.site_city) {
        address += ', ' + item.site_city;
      }

      if (item.site_country) {
        address += ', ' + item.site_country;
      }

      return address;
    },
    async syncDevices() {
      this.isFetchingMinionDevices = true;
      this.isFetchingDevices = true;
      await this.fetchMinionDevices();
      await this.fetchDevices();
    },
    fetchMinionDevices() {
      return minionDeviceService
        .fetch()
        .then(data => {
          this.minionDevicesMap = data;
          console.log('miniion devices', data);
          this.minionDevices = Object.keys(data).map(key => ({
            ...this.minionDevicesMap[key],
            isDeleting: false,
            site_address: this.formatAddress(data[key]),
          }));
          console.log(this.minionDevices);
        })
        .catch(err => {
          this.$toasted.show('Error fetching minion devices', { type: 'error' });
        })
        .finally(() => {
          this.isFetchingMinionDevices = false;
        });
    },
    fetchDevices() {
      return minionDeviceService
        .discoverDevices()
        .then(data => {
          this.devicesMap = data;
          this.discoveredDevices = Object.keys(this.devicesMap)
            .filter(key => !this.minionDevicesMap[key])
            .map(key => ({
              ...this.devicesMap[key],
              isUpdating: false,
              site_address: this.formatAddress(this.devicesMap[key]),
            }));
        })
        .catch(err => {
          console.log(err);
          this.$toasted.show('Error fetching remote devices', { type: 'error' });
        })
        .finally(() => {
          this.isFetchingDevices = false;
        });
    },
    createMinions(minion) {
      minion.isUpdating = true;
      let createMinionService;
      if (Array.isArray(minion) && minion.length > 0) {
        createMinionService = minionDeviceService.bulkCreate;
      } else {
        createMinionService = minionDeviceService.create;
      }
      return createMinionService(minion)
        .then(() => {
          this.$toasted.show('Successfully updated the device status.');
          this.syncDevices();
        })
        .catch(() => {
          this.$toasted.show('Error occured while updating the device status', { type: 'error' });
        })
        .finally(() => {
          minion.isUpdating = false;
        });
    },
    removeMinions(minion) {
      minion.isDeleting = true;

      let removeMinionService;
      if (Array.isArray(minion) && minion.length > 0) {
        removeMinionService = minionDeviceService.bulkRemove;
      } else {
        removeMinionService = minionDeviceService.remove;
      }
      return removeMinionService(minion)
        .then(() => {
          this.$toasted.show('Successfully remove the device as slave.');
          this.syncDevices();
        })
        .catch(() => {
          this.$toasted.show('Error occured while updating the device status', { type: 'error' });
        })
        .finally(() => {
          minion.isDeleting = false;
        });
    },
  },
};
</script>
