<template>
  <div>
    <v-menu offset-y>
      <template v-slot:activator="{ on, attrs }">
        <v-btn x-small color="primary" class="white--text mr-2" v-bind="attrs" v-on="on"
          >Import<v-icon x-small right dark>mdi-import</v-icon></v-btn
        >
      </template>
      <v-list>
        <v-list-item link>
          <v-list-item-title
            @click="
              dialogImportJSON = true;
              dataImport = '';
            "
            >Json</v-list-item-title
          >
        </v-list-item>
        <v-list-item link>
          <v-list-item-title
            @click="
              dialogImportExcel = true;
              file = null;
            "
            >Excel</v-list-item-title
          >
        </v-list-item>
      </v-list>
    </v-menu>
    <v-dialog v-model="dialogImportExcel" width="500px">
      <v-card>
        <v-card-title>Choose excel file</v-card-title>
        <v-card-text>
          <v-file-input outlined dense label="File input" v-model="file" accept=".xls,.xlsx"></v-file-input>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="dialogImportExcel = false">Close</v-btn>
          <v-btn color="primary darken-1" text @click="importFile" :disabled="!file">Import</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="dialogImportJSON" width="500px">
      <v-card>
        <v-card-title>Import via JSON</v-card-title>
        <v-card-text>
          <v-container>
            <v-textarea outlined name="input-7-4" label="import data" v-model="dataImport"></v-textarea>
          </v-container>
        </v-card-text>
      </v-card>
      <v-card-actions>
        <v-btn color="primary darken-1" text @click="importJson" :disabled="!dataImport">Import</v-btn>
      </v-card-actions>
    </v-dialog>
  </div>
</template>
<script>
import * as xlsx from 'xlsx';
export default {
  name: 'import-dialog',
  props: ['receiveDataRows'],
  data() {
    return {
      file: null,
      dialogImportExcel: false,
      dialogImportJSON: false,
      dataImport: '',
    };
  },
  methods: {
    importFile() {
      let fileReader = new FileReader();
      fileReader.readAsBinaryString(this.file);
      fileReader.onload = this.handleFileLoad;
    },
    handleFileLoad(e) {
      let data = e.target.result;
      let workbook = xlsx.read(data, { type: 'binary' });
      const firstSheet = Array.isArray(workbook.SheetNames) && workbook.SheetNames.length > 0 && workbook.SheetNames[0];
      if (!firstSheet) {
        return this.$toasted.show('Invalid Excel File. Please reupload.', { type: 'error' });
      }
      const rowObject = xlsx.utils.sheet_to_row_object_array(workbook.Sheets[firstSheet]);
      this.receiveDataRows(rowObject);
      this.dialogImportExcel = false;
    },
    importJson() {
      try {
        const dataItems = JSON.parse(this.dataImport);
        this.receiveDataRows(dataItems);
        this.dialogImportJSON = false;
      } catch (err) {
        return this.$toasted.show('Invalid JSON', { type: 'error' });
      }
    },
  },
};
</script>
